import * as React from "react";

import { Select } from '@chakra-ui/react';

const SelectDropdown = (props) => {
    const {value, placeholder, values, onChange} = props;
    return (
        <Select bg='white' value={value} variant='filled' placeholder={placeholder} onChange={e => onChange(e.target.value)}>
            {values.map((opt) => {
                return (<option bg='white' key={opt.value} value={opt.value}>{opt.name}</option>);
            })} 
        </Select>
    );
};

export default SelectDropdown;